<template>
    <Mensege :msg="msg" v-show="msg"/> 
    <table id="burger-table">
        <table>
            <tr id="burger-table-heding">
                <td class="order-id">#</td>        
                <td>Cliente:</td>        
                <td>P찾o:</td>        
                <td>Carne:</td>        
                <td>Opcionais:</td>        
                <td>Ac천es:</td>
        </tr>
        </table>
        <table id="burger-table-rows"  v-for="burger in burgers" :key='burger.id'>
            <tr class="burger-table-row">
                <td class="order-number">{{burger.id}}</td>
                <td>{{burger.nome}}</td>
                <td>{{burger.pao}}</td>
                <td>{{burger.carne}}</td>
                <td>
                    <ul>
                        <li  v-for="(opcional, index) in burger.opcionais" :key="index">
                            {{opcional}}
                        </li>
    
                    </ul>
                </td>
                <td>
                    <select name="status" class="status" @change="updatePedidos($event, burger.id)">
                        <option value="">Selecione</option>
                        <option v-for="s in status" :key="s.id" :value="s.tipo" :selected="burger.status == s.tipo">
                            {{s.tipo}}
                        </option>
                    </select>
                    <button class="delete-btn" @click="deletePedidos(burger.id)">Cancelar</button>
                    
                </td>
            </tr>
        </table>
    </table>
</template>

<script>
import Mensege from './Mensege.vue';
export default {
    name: "dashboard",
    data () {
        return {
            burgers: null,
            burger_id: null,
            status: [],
            msg: null
        } 
    },
    components: {
        Mensege
    },
    methods: {
        // fazer uma requisic찾os  dos dados no banco 
        async getPedidos(){ 
            // rota da requisic찾o trazendo os dados
            const req = await fetch("http://localhost:3000/burgers"); 
            // transformar dados em json
            const data = await req.json(); 
            // enviar os dados para a variavel burgers do data
            this.burgers = data; 
            console.log(this.burgers);

            //registrar status
            this.getStatus();
        },
        async getStatus(){ 
            const req = await fetch("http://localhost:3000/status");
            const data = await req.json();

            this.status = data;
        },
        async deletePedidos(id) {
        
            const req = await fetch(`http://localhost:3000/burgers/${id}`,{
                method: "DELETE"
            });
            const res = await req.json();

            //Mensagem de CANCELADO na tela
            this.msg =`Pedido Cancelado com sucesso`

            setTimeout(()=> this.msg ='',3000)
          
            this.getPedidos();
        },
        async updatePedidos(event,id){
            const option = event.target.value;

            const dataJson = JSON.stringify({status: option});

            const req = await fetch(`http://localhost:3000/burgers/${id}`,{
                method: "PATCH",
                headers: {"content-type":"application/json"},
                body: dataJson
            });
            const res = await req.json();

            this.msg =`Status do pedido ${res.id} foi atualizaso para ${res.status}`;

            setTimeout(()=> this.msg ='',3000)
        }   
    },
    mounted() {
        this.getPedidos();
    }
}
</script>

<style scoped>
#burger-table {
    max-width: 1200px ;
    margin: 0 auto;

}
#burger-table-heding,
#burger-table-rows,
.burger-table-row  {
    display: flex;
    flex-wrap: wrap;
    width: 1200px;
}

#burger-table-heding { 
    font-weight: bold;
    padding: 12px;
    border-bottom: 3px solid #333;   
}

#burger-table-heding td,
.burger-table-row td {
    width: 19%;
}


#burger-table-rows {
    padding: 12px;
    border-bottom: 1px solid #ccc;
}
#burger-table-heding .order-id,
.burger-table-row .order-number {
    width: 5%;
}
select {
    padding: 12px 6px;
    margin-right: 12px;
}
.delete-btn {
    background-color: #222;
    color: #FBCA03;
    font-weight: bold;
    border: 2px solid #222;
    padding: 10px;
    font-size: 16px;
    margin: 0 auto;
    cursor: pointer;
    transition: .5s;
}
.delete-btn:hover {
    background-color: transparent;
    color: #222;
}

</style>